.navbar{
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
}

.btn{
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
}
.meme--box{
  position: relative;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-shadow:
      2px 2px 0 #000,
      -2px -2px 0 #000,
      2px -2px 0 #000,
      -2px 2px 0 #000,
      0 2px 0 #000,
      2px 0 0 #000,
      0 -2px 0 #000,
      -2px 0 0 #000,
      2px 2px 5px #000;
}
.meme-img{
  width: 100%;
  border-radius: 5px;
}
.meme--text{
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
  align-items: center;
  justify-content: center;
  justify-self: center;
  
}
.top-text{
  top:2rem ;
}
.bottom-text{
  bottom: 2rem ;
}
